.header-main {
    background-color: #116530;
    height: 450px;
    padding: 50px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
#title-info {
    display: flex;
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 320;
    letter-spacing: 0.2em;
    padding-top:60px;
    flex-direction: column;
    margin-left: 50px;
    column-gap: 10px;
}
#name-title {
    color: white;
    font-size: 64px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 320;
    letter-spacing: 0.2em;
}
#headshot {
    width:350px;
    height: 350px;
    align-self: center;
    object-fit: cover;
    object-position: 0 0%;
    margin-left: 5%;
}
.clip-polygon {
  clip-path: polygon(32% 0, 100% 28%, 90% 86%, 22% 100%, 0 34%);
  /* Note that percentages work as well as px */
}
.arrow-down {
  width: 0; 
  height: 0; 
  border-left: 60px solid transparent;
  border-right: 60px solid transparent;
  border-top: 40px solid #116530;
  margin: auto;
}